








































































@import "../../../assets/design/index.less";
.poster {
    width: 100%;
    height: 100%;
    overflow: hidden;

    img {
        width: 100%;
        vertical-align: top;
    }

    :global {
        .ant-upload.ant-upload-select-picture-card {
            width: 100%;
            height: 100%;
            border: transparent;
        }
        .ant-upload.ant-upload-select-picture-card > .ant-upload {
            padding: 0;
        }
        .ant-upload.ant-upload-select-picture-card {
            margin: 0;
        }
    }
}

@primary-color: #1b1b1b;@item-hover-bg: #ddd;@border-radius-base: 2px;@input-hover-border-color: #0d499e;