.editmovie {
    width: 700px;
    max-height: 600px;
    margin:  0 auto;
    border-radius: @BorderRadius;
    display: flex;
    flex-direction: row;
    overflow: hidden;
    box-shadow: 0 0 10px 1px #ececec;
}

.movieimages {
    width: 400px;
}

.movieform {
    width: 300px;
    padding: 40px 20px;
    background-color: #ffffff;
}

.colsubmit {
    :global {
        .ant-form-item-children {
            display: inherit;
            text-align: center;
        }
    }
}

.submitbtn {
    margin: 0 auto;
    border-radius: @BorderRadius;
}

@primary-color: #1b1b1b;@item-hover-bg: #ddd;@border-radius-base: 2px;@input-hover-border-color: #0d499e;